.frame:hover > .show_detail {
    visibility: visible;
} 
*/
.show_detail {
    position: absolute;
    top: 90px;
    width: 360px;
    height: 360px;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
    padding-top: 30px;
    border-radius: 20px;
    color: rgb(51, 51, 51);
    visibility: hidden;
    z-index: 10;
    cursor: default;
    -ms-user-select: none; 
    -moz-user-select: -moz-none; 
    -webkit-user-select: none; 
    -khtml-user-select: none; 
    user-select:none;
}
.imgbox {
    object-fit: cover;
    width: 354px;
    height: 354px;
    margin: 2px;
    border: 1px solid #aaaaaa8e;
}
.option {
    display: inline-block;
    padding: 0;
    margin: 0;
    margin-left: 0px;
    margin-top: -5px;
    font-size: 35px;
    color: #ff6f6f;
    vertical-align: top;
    letter-spacing: -3px;
    padding-right: 5px;
    border: 3px solid #ff6f6f;
}
.order {
    display: inline-block;
    padding: 3px;
    margin: 0;
    margin-left: -5px;
    margin-top: -5px;
    font-size: 35px;
    color: white;
    vertical-align: top;
    background-color: #ff6f6f;
}
.item_nm {
    display: block;
    margin: 0;
    padding: 0;
    font-size: 25px;
    margin-top: 10px;
    letter-spacing: -2px;
    margin-bottom: 10px;
    margin-left: 10px;
    color: #292929;
}
.item_option {
    display: block;
    margin-left: 10px;
}
.item_option_type {
    display: inline-block;
    color: #ff6f6f;
    width: 60px;
    margin: 0;
    padding: 0;
    font-size: 15px;
}
.item_option_val {
    display: inline-block;
    color: #5a5a5a;
    width: 270px;
    margin: 0px;
    padding: 0px;
    white-space: normal;
    vertical-align: top;
    font-size: 15px;
}
.price_container {
    display: block;
    margin: 0;
    padding: 0;
    margin-top: 30px;
    text-align: center;
}
.old_price {
    display: inline-block;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    color: #5a5a5a;
    text-decoration: line-through;
    font-size: 20px;
}
.to {
    display: inline-block;
    margin: 0;
    padding: 0;
    margin-left: 10px;
    margin-right: 10px;
    vertical-align: middle;
    font-size: 30px;
}
.price {
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 170px;
    vertical-align: middle;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 30px;
    color: #17aa2b;
    border: 3px solid #17aa2b;
    animation-name: price_color;
    animation-duration: .5s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes price_color {
    from {
        color: #17aa2b;
        border: 3px solid #17aa2b;
    }
    to {
        color: #2af345;
        border: 3px solid #2af345;
    }
}